const location = window.location;
const { protocol, hostname, port } = location;
let origin = location.origin;

if (!origin) {
  origin = `${protocol}//${hostname}${port ? `:${port}` : ''}`;
}

export function getViewerUrl(metaId) {
  return `${origin}/viewer/?id=${metaId}`;
}

export function getEditOwnUrl(metaId) {
  return `${origin}/editor/index.html?id=${metaId}`;
}

export function getEditFolkUrl(metaId) {
  return `${origin}/editor/index.html?fid=${metaId}`;
}
export default {};



