require('src/autologin')();
import 'font-awesome/css/font-awesome.css';
import 'jquery';
import 'jquery.event.move';
import Vue from 'vue';

const eventHub = new Vue();

Vue.mixin({
  beforeCreate() {
    this.eventHub = eventHub;
  },
});

import App from './pcenterApp';

/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: h => h(App),
});





