<template>
  <div>
    <h1><a href="/editor/index.html"></a></h1>
    <table border="1">
      <tbody>
      <tr v-for="(item,index) in list">
        <td>{{item.title}}</td>
        <td><a :href="getViewHref(item,index)" class="pointer"></a></td>
        <td><a @click="del(item,index)" class="pointer"></a></td>
        <td><a target="_blank" :href="getEditHref(item)"></a></td>
        <td><a target="_blank" :href="getFolkEditHref(item)"></a></td>
      </tr>
      </tbody>
    </table>

  </div>
</template>

<script>
  import {
    getMetaList as apiGetMetaList,
    delMeta as apiDelMeta,
  } from 'api';

  import {
  getViewerUrl,
  getEditOwnUrl,
  getEditFolkUrl
} from '../../helpers/urlMgr';

  export default {
    components: {},
    data () {
      return {
        list: null
      };
    },
    beforeCreate () {
      apiGetMetaList().then(list => {
        this.list = list;
      });
//        GetMetaList
//        this.getMetaList
    },
    methods: {
      del(item, index) {
        apiDelMeta(item.objectId);
        this.list.splice(index, 1)
      },
      getEditHref(item) {
        return getEditOwnUrl(item.objectId);
      },
      getFolkEditHref(item) {
        return getEditFolkUrl(item.objectId);
      },
      getViewHref(item) {
        return getViewerUrl(item.objectId);
      },
    }
  };

</script>

<style lang="scss" rel="stylesheet/scss">
  table {
    border-collapse: collapse;
    width: 100%;
  }

  tr:hover {
    background-color: lightyellow;
  }

  .pointer {
    cursor: pointer;
  }
</style>



